@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700;800&family=Poppins:wght@100;300;400;500;600;700;800&family=Roboto:ital,wght@0,400;0,700;1,100;1,300;1,500&display=swap');
body {
  background: #0f0c29;  
  background: -webkit-linear-gradient(to right, #24243e, #302b63, #0f0c29);  
  background: linear-gradient(to right, #24243e, #302b63, #0f0c29); 
}
* {
  font-family: 'Poppins', sans-serif;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
}
:root {
  --color-one: #09cce5;
  --color-one-part-two: #64dbff;
  --color-two: #302b63;
  --color-three: #110e2c;
  --color-light: rgba(255, 255, 255, 0.6);
  --container-width-lg: 75%;
  --container-width-md: 75%;
  --container-width-sm: 90%;
  --trans: 0.4s;
}
.text-light {
  color: var(--color-light);
}
h1, h2, h3, h4, h5 {
  color: var(--color-one);
  text-align: center;
} 
li {
  list-style: none;
}
a {
  text-decoration: none;
  color: var(--color-one-part-two);
}
.btn {
  padding: 8px 22px;
  font-size: 18px;
  border: none;
  outline: none;
  font-weight: 600;
  text-transform: capitalize;
  cursor: pointer;
  background-color: var(--color-one-part-two);
  color: var(--color-two);
  border-radius: 8px;
  width: fit-content;
  transition: var(--trans);
  border: 1px solid transparent;
}
.btn:hover {
  background-color: transparent;
  color: #fff;
  border-color: var(--color-one);
}
section {
  margin-top: 8rem;
}
section > h2, section > h5 {
  text-align: center;
  color: var(--color-light);
}
section > h2 {
  color: var(--color-one);
  font-size: 28px;
  margin-bottom: 3rem;
}
.container {
  width: var(--container-width-lg);
  margin: 0 auto;
}
@media (max-width: 991px) {
  .container {
    width: var(--container-width-md);
  }
  section {
    margin-top: 6rem;
  }
}
@media (max-width: 600px) {
  .container {
    width: var(--container-width-sm);
  }
  section {
    margin-bottom: 2rem;
  }
}