.container__about {
    display: flex;
    gap: 20%;
    margin-bottom: 100px;
}
.about__image {
    background: linear-gradient(45deg, transparent, var(--color-one), transparent);
    border-radius: 1rem;
    aspect-ratio: 1/1;
    height: 100%;
}
.about__image-img {
    transform: rotate(10deg);
    border-radius: 1rem;
    display: block;
    transition: var(--trans);
}
.about__image-img:hover {
    transform: rotate(0);
}
.container__about .boxs {
    display: flex;
    gap: 20%;
    margin-bottom: 2rem;
}
.container__about .box {
    background: #110e2c;
    padding: 3rem 1.3rem;
    border-radius: 1rem;
    width: 200px;
    transition: var(--trans);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: var(--color-one);
    gap: 10px;
    font-size: 18px;
    cursor: default;
    border: 1px solid transparent;
}
.container__about .box:hover {
    background-color: transparent;
    color: #FFF;
    border-color: var(--color-one);
}
.icons__abouts {
    font-size: 22px;
}
.container__about p {
    color: var(--color-light);
    margin-bottom: 1.5rem;
}
@media (max-width: 991px) {
    .container__about {
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        gap: 3rem;
    }
    .container__about .allInfo {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    .container__about p {
        text-align: center;
        width: 85%;
    }
}