.container__portfol {
    width: 95%;
}
.boxs__portfolio {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 5%;
}
.box__portfolio {
    background: var(--color-three);
    padding: 3rem 2rem;
    margin-bottom: 25px;
    border-radius: 1rem;
    border: 1px solid transparent;
    transition: var(--trans);
}
.box__portfolio:hover {
    background-color: transparent;
    border-color: var(--color-one)
}
.box__portfolio h6 {
    margin: 25px 0;
    font-size: 20px;
    color: #FFF;
    font-weight: 400;
}
.btns__portfolio {
    display: flex;
    gap: 5%;
}
.box__portfolio img {
    width: 300px;
    height: 150px;
}