.container__testmonials {
    width: 45%;
}
.box__testmonials {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: var(--color-three);
    /* width: auto; */
    margin: 0 auto;
    padding: 3rem 1.5rem;
    border-radius: 30px;
    /* max-width: 550px; */
    cursor: default;
}
.boxs__testmonials img {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    border: 6px solid var(--color-two);
}
.box__testmonials h3 {
    margin: 15px 0;
    font-size: 20px;
    font-weight: 500;
    color: #FFF;
}
.box__testmonials p {
    color: var(--color-light);
    font-size: 15px;
    line-height: 1.7;
    font-weight: 400;
    width: 80%;
    margin: 0 auto;
}
@media (max-width: 991px) {
    .container__testmonials {
        width: 65%;
    }
}
@media (max-width: 600px) {
    .container__testmonials {
        width: var(--container-width-sm);
    }
}