@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&family=Open+Sans:wght@300;400;500;600;700;800&family=Poppins:wght@100;300;400;500;600;700;800&family=Roboto:ital,wght@0,400;0,700;1,100;1,300;1,500&display=swap');

footer {
    background-color: var(--color-one);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 60px 0 125px 0;
    margin-top: 80px;
}

footer .mainh2__footer {
    color: var(--color-three);
    font-family: 'Dancing Script', cursive;
    margin-bottom: 45px;
    font-size: 30px;
    font-weight: 600;
    transition: var(--trans);
}

.links__footer {
    display: flex;
    margin-bottom: 55px;
    gap: 5%;
    justify-content: center;
    align-items: center;
}

.links__footer a {
    color: var(--color-three);
    transition: var(--trans);
    text-transform: capitalize;
}

.links__footer a:hover {
    color: #fff;
}

.socias__icons__footer {
    display: flex;
    margin-bottom: 65px;
    gap: 30px;
    justify-content: center;
}

.socias__icons__footer a {
    color: white;
    background: var(--color-three);
    padding: 0.6rem 0.8rem;
    border-radius: 12px;
    border: 1px solid transparent;
    transition: var(--trans);
}

.socias__icons__footer a:hover {
    background-color: transparent;
    border-color: var(--color-three);
    color: var(--color-three);
}

.copyright {
    font-size: 12.5px;
}

@media (max-width: 600px) {
    .links__footer {
        flex-direction: column;
        gap: 1.7rem;
    }
}