.container__skills {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 5%;
}
.container__skills .front, .container__skills .back {
    background-color: var(--color-three);
    padding: 3rem 3.5rem;
    border-radius: 25px;
    transition: var(--trans);
}
.container__skills .front:hover, .container__skills .back:hover {
    background-color: transparent;
}
.container__skills .front > h2, .container__skills .back > h2 {
    margin-bottom: 15px;
    font-size: 20px;
    font-weight: 600;
}
.boxs__skills {
    display: grid;
    grid-template-columns: 1fr 1fr;
}
.boxs__skills .box {
    display: flex;
    padding: 10%;
}
.icon__skill {
    color: var(--color-one);
    margin: 5px 10px 0px 0px;
}
.boxs__skills .info h4 {
    color: #FFF;
    font-weight: 600;
    display: flex;
}
@media (max-width: 991px) {
    .container__skills {
        grid-template-columns: 1fr;
    }
    .container__skills .front, .container__skills .back {
        margin: 0 auto;
        width: 80%;
    }
}
@media (max-width: 600px) {
    .container__skills {
        grid-template-columns: 1fr;
    }
    .container__skills .front, .container__skills .back {
        padding: 2rem 0.8rem;
        width: 100%;
    }
}