#home h4 {
    color: #fff;
}
#home h2 {
    margin-bottom: 1rem;
}
#home h5 {
    margin-bottom: 4rem;
}
#home .container {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}
#home .btns {
    margin-bottom: 3rem;
    display: flex;
    gap: 10%;
    width: 325px;
}
.cv__btn {
    background-color: transparent;
    color: var(--color-one);
    border: 1px solid var(--color-one);
}
.header__icons {
    display: flex;
    flex-direction: column;
    gap: 10px;
    position: relative;
    margin-bottom: 110px;
}
.header__icons a:hover {
    color: #fff;
}
.header__icons::after {
    content: "";
    height: 45px;
    width: 2px;
    background-color: var(--color-one);
    position: absolute;
    bottom: -50px;
    left: 50%;
    transform: translateX(-50%);
}
.scrool__down {
    transform: rotate(90deg);
}
.scrool__down a:hover {
    color: #fff;
}