#contact {
    margin-bottom: 10rem;
}
.contact__container {
    display: grid;
    grid-template-columns: 30% 50%;
    gap: 10%;
    width: 63%;
}
.boxs__contact {
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.box__contact {
    background: var(--color-three);
    padding: 2rem 1.5rem;
    width: 220px;
    text-align: center;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: #fff;
    border: 1px solid transparent;
    transition: var(--trans);
    cursor: default;
}
.box__contact:hover {
    border-color: var(--color-one);
    background-color: transparent;
}
.icons__contact {
    font-size: 25px;
}
.box__contact h6 {
    margin: 10px 0;
    font-size: 18px;
    font-weight: 500;
}
.input__contact {
    display: flex;
    flex-direction: column;
    gap: 30px;
}
.input__contact input, .input__contact textarea {
    background: transparent;
    border: 2px solid var(--color-three);
    outline: navajowhite;
    padding: 1.1rem 1rem;
    border-radius: 10px;
    color: #fff;
    resize: none;
}
.input__contact input:focus, .input__contact textarea:focus {
    border-color: var(--color-one)
}
@media (max-width: 991px) {
    .contact__container {
        grid-template-columns: 1fr;
    }
    .box__contact {
        width: 100%;
    }
}
@media (max-width: 600px) {
    .contact__container {
        width: var(--container-width-sm);
    }
}