nav {
    background-color: rgba(0, 0, 0, 0.3);
    width: max-content;
    padding: .8rem 1.8rem;
    position: fixed;
    z-index: 2;
    left: 50%;
    transform: translateX(-50%);
    top: -5rem;
    display: flex;
    gap: 1rem;
    border-radius: 10rem;
    backdrop-filter: blur(15px);
    transition: var(--trans);
}
nav a {
    padding: 1rem;
    border-radius: 50%;
    display: flex;
    color: #fff;
    transition: var(--trans);
}
nav a:hover {
    background-color: var(--color-two);
}
nav a.active {
    background-color: var(--color-one);
    color: var(--color-three);
}
.upAndDown {
    top: 1rem;
}
