.boxs__services {
    display: flex;
    justify-content: center;
    gap: 5%;
}
.box__service {
    background: var(--color-three);
    border-bottom-left-radius: 35px;
    border-bottom-right-radius: 35px;
    height: fit-content;
    border: 1px solid var(--color-one);
    transition: var(--trans);
    cursor: default;
    max-width: 350px;
}
.box__service:hover {
    background-color: transparent;
}
.box__service > h2 {
    background: var(--color-one);
    color: var(--color-three);
    font-size: 20px;
    font-weight: 500;
    padding: 30px;
    border-radius: 0 0 20px 20px;
}
.info__services {
    padding: 2rem;
    display: flex;
    flex-direction: column;
    gap: 25px;
}
.icon__services {
    color: #FFF;
    font-size: 1.8rem;
    margin-top: 2px;
}
.artical__services {
    display: flex;
    gap: 5%;
}
.artical__services p {
    color: #FFF;
    font-size: 15px;
    font-weight: 400;
}
@media (max-width: 991px) {
    .container__services {
        width: var(--container-width-sm);
    }
}
@media (max-width: 767px) {
    .boxs__services {
        flex-direction: column;
        align-items: center;
    }
    .box__service:not(:last-child) {
        margin-bottom: 50px;
        
    }
}